:root {
    --next-link-color: #cd51e5;
}

* {
    box-sizing: border-box;
}

body {
    position: relative;
    display: block;
    min-width: 100vw;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    margin: 0;
    padding: 0;
    background-image: linear-gradient(90deg, #3b0785, #f822ff);
    /* background-color: black; */
}

.three {
    position: absolute;
    display: block;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.three>canvas {
    display: block;
    height: 100%;
    width: 100%;
}

.container {
    pointer-events: none;
    position: absolute;
    display: grid;
    grid-template-rows: 10% 85% 5%;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    color: white;
    transition: all 1s ease-in;
    box-shadow: 10px 10px 197px 10px rgba(0, 0, 0, 0.808) inset;
}

.overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 0;
    opacity: 1;
    box-shadow: 10px 10px 197px 10px rgba(0, 0, 0, 0.808) inset;
}

.overlay1 {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 1;
    opacity: 0;
    /* box-shadow: 10px 10px 197px 190px rgba(0, 0, 0, 0.808) inset; */
}

.container>.nav {
    position: relative;
    
}

.nav-cont {
    position: relative;
    width: 100%;
    max-width: 100vw;
    display: grid;
    grid-template-columns: 70% 15% 15%;
    padding: 25px 50px;
    z-index: 3;
    overflow-x: hidden;
    /* background-color: rgba(0, 0, 0, 0.664);
    box-shadow:
        12px 12px 16px 0 rgba(0, 0, 0, 0.25),
        -8px -8px 12px 0 rgba(0, 0, 0, 0.3); */
}


.nav-cont>div {
    font-family: SpeziaMonoTrial, Helvetica, sans-serif;
    font-weight: 500;
    padding: 10px 20px;
    letter-spacing: 0.5em;
    font-size: 0.625rem;
    border-top: 0.9px rgba(255, 255, 255, 0.3) solid;
    text-align: center;
    pointer-events: all;
    cursor: pointer;
    transition: opacity 0.2s ease-out;
    opacity: 0.7;
}


.nav-cont>div:hover {
    opacity: 1;
}

.nav-cont>div>p {
    margin: 0;
    padding: 0;
    transform: translateY(170%);

}

.nav-cont>div.active {
    border-top: 2.9px #cf08f7 solid;
    border-left: 2.9px #cf08f7 solid;
    border-right: 2.9px #cf08f7 solid;
    color: #ffffff
}

.nav-cont>div {
    border-top: 0.9px rgba(255, 255, 255, 0.7) solid;
    border-left: 0.9px rgba(255, 255, 255, 0.7) solid;
    border-right: 0.9px rgba(255, 255, 255, 0.7) solid;
}



/* 
.content{
    display: none;
} */

.idk > div{
    overflow: hidden;
}

h2 {
    font-family: "Manrope";
    font-weight: 300;
    font-size: 3rem;
    margin: 5px 0;
    /* background: rgba(86, 7, 133, 0.788); */
    max-width: max-content;
    padding: 3px 0;
}

.line-anime {
    font-family: "Manrope";
    font-weight: 500;
    font-size: 1.1rem;
    margin: 15px 0px;
    margin-top: 50px;
    color: var(--next-link-color);
    border: none;
    background-color: transparent;
    pointer-events: all;
    cursor: pointer;
    overflow-x: hidden;
}

.line-anime>.line {
    margin-bottom: 20px;
    width: 100%;
    height: 1.8px;
    background-color: var(--next-link-color);
}

.line-anime>div:not(.line)>div {
    position: relative;
    display: inline-block;
}

.line-anime>div:not(.line)>div:nth-of-type(2) {
    margin-left: 55px;
    padding-right: 20px;
}


.icon-circle {
    position: relative;
    display: grid;
    grid-template-columns: 100%;
    place-items: center;
    height: 20px;
    width: 20px;
    border-radius: 100%;
    border: 2px var(--next-link-color) solid;
    transform: translateY(-1px);
}

.icon-circle>svg {
    /* height: 10px;
    width: 10px; */
    fill: var(--next-link-color)
}

.first {
    position: relative;
    display: grid;
    grid-template-columns: 50% 50%;
    /* align-items: ; */
    justify-items: center;
    height: 100%;
}

.first>div:nth-of-type(2)>.idk {
    margin-top: 30%;
}

.second {
    position: relative;
    height: 100%;
    width: 100%;
}

.second>div {
    position: relative;
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 100%;
    align-items: center;
    padding: 100px 10vw;
}

.project-container {
    /* background: rgba(86, 7, 133, 0.788); */
    /* background: rgba(0, 0, 0, 0.219);
    width: max-content;
    border: 2px rgba(0, 0, 0, 0.788) solid; */
    /* border-radius: 10px; */
    padding: 40px;
    background-color: rgba(0, 0, 0, 0.664);
    box-shadow:
        12px 12px 16px 0 rgba(0, 0, 0, 0.25),
        -8px -8px 12px 0 rgba(0, 0, 0, 0.3);
    width: max-content;
    /* min-height: 60vh;
    min-width: 15vw; */
}

/* .p2 > .project-container{
    background: rgba(0, 0, 0, 0.72);
}

.p3 > .project-container{
    background: rgba(255, 34, 34, 0.502);
} */

.socials {
    position: relative;
    max-width: 50%;
    display: flex;
    width: max-content;
    margin-left: auto;
    margin-right: 20px;
    font-family: Manrope, Helvetica, sans-serif;
    z-index: 3;
    transition: all 1s ease-out;
}

.socials>div {
    position: relative;
    display: grid;
    grid-template-columns: auto auto;
    place-items: center;
    margin: 0 20px;
    pointer-events: all;
    cursor: pointer;
    opacity: 0.7;
    transition: opacity 0.3s ease-out;
    margin: 0;
    margin-right: 30px;
}

.socials>div:hover {
    opacity: 1;
}

.socials > div > div{
    position: relative;
    overflow-x: hidden;
    margin-right: 15px;
}

.socials > div > div > p {
    margin: 0;
    padding: 0;
    display: block;
    width: 0;
}

.socials>div>img {
    width: 20px;
}


@media(max-height: 860px) {
    h2 {
        font-size: 2.3rem;
    }

    .line-anime {
        font-size: 1rem;
    }
}


@media(max-height: 710px) {
    h2 {
        font-size: 1.8rem;
    }
}


@media(max-height: 590px) {
    h2 {
        font-size: 1.4rem;
    }

    .line-anime {
        font-size: 0.9rem;
    }
}

@media(max-width: 1200px) and (min-height: 860px) {
    h2 {
        font-size: 2.3rem;
    }
}


@media(max-width: 900px) {


    .nav-cont {
        padding: 10px 10px;
        grid-template-columns: "50% 25% 25%";
        /* opacity: 1; */
    }

    .nav-cont>div {
        letter-spacing: 0.2em;
        font-size: 0.525rem;
        border-top: 0.9px rgba(255, 255, 255, 0.3) solid;
        text-align: center;
        pointer-events: all;
        cursor: pointer;
        transition: opacity 0.2s ease-out;
        opacity: 1;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
    }

    .nav-cont>div.active {
        border-top:   0.9px #cf08f7 solid;
        border-left:  0.9px #cf08f7 solid;
        border-right: 0.9px #cf08f7 solid;
        color: #ffffff
    }


    .container{
        box-shadow: 0px 0px 197px 30px rgba(0, 0, 0, 0.808) inset;
    }

    /* .overlay{
        box-shadow: 0px 0px 197px 30px rgba(0, 0, 0, 0.808) inset;
    } */

    .overlay1 {
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: 1;
        opacity: 0;
        box-shadow: 10px 10px 197px 50px rgba(0, 0, 0, 0.808) inset;
    }


    h2 {
        font-family: "Manrope";
        font-weight: 200;
        font-size: 1.1rem;
        margin: 0px 0;
        /* background: rgba(86, 7, 133, 0.788); */
        max-width: max-content;
        padding: 3px 0;
    }

    .line-anime {
        font-family: "Manrope";
        font-weight: 500;
        font-size: 0.8rem;
        margin: 15px 0px;
        margin-top: 30px;
        color: var(--next-link-color);
        border: none;
        background-color: transparent;
        pointer-events: all;
        cursor: pointer;
        overflow-x: hidden;
    }

    .line-anime>.line {
        margin-bottom: 20px;
        width: 100%;
        height: 1.3px;
        background-color: var(--next-link-color);
    }


    .line-anime>div:not(.line)>div:nth-of-type(2) {
        margin-left: 15px;
        padding-right: 20px;
    }

    .icon-circle {
        border: 1px var(--next-link-color) solid;
    }

    .first {
        grid-template-columns: 0% 100%;
        align-items: center;
        justify-items: right;
        height: 100%;
    }

    .first>div:nth-of-type(2) {
        margin-right: 20px;
        transform: translateY(-7%);
    }

    .second>div {
        padding: 100px 0px;
    }

    .project-container{
        /* box-shadow: none;
        background-color: transparent; */
        padding: 20px;
        margin-left: 20px;
    }

    .socials {
        max-width: 100%;
        align-items: center;
        justify-items: left;
        width: max-content;
        margin-left: auto;
        margin-right: auto;
    }

    .socials>div {
        position: relative;
        margin: 0 10px;
        /* margin-right: 10px; */
    }


    .socials > div > div > p {
        margin: 0;
        margin-left: 15px;
        font-size: 0.7rem;
        padding: 0;
        display: block;
        width: 100%;
    }

    .socials>div>img {
        /* height: 20px; */
        width: 15px;
    }


}

@media(orientation: landscape) and (max-height: 560px){
    body{
        min-height: 560px;
    }
}



.hide-project {
    display: none !important;
}

.hide-section {
    display: none;
    max-height: 100vh;
}